import React from 'react';
import { NavLink } from 'react-router-dom';

const Vacancy = (params) => {
    return (
        <NavLink className="vacancy__list-item" to={`/questionnaire/${params.vacancy.id}`}>
            <div className="vacancy__list-item__content">
                <img src={params.vacancy.picture} className="vacancy__list-item__bg" />
                <div className="vacancy__list-item__text-wrapper">
                    <div className="vacancy__list-item__content-text">{params.vacancy.text}</div>
                </div>
                <div className="vacancy__list-item__name h3">{params.vacancy.name}</div>
            </div>
        </NavLink>
    )
}

export default Vacancy;